
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

p {
  margin-bottom: 0.2rem;
}
.transfer-button {
  background-color: #c7088e;
  border: none;
  color: white;
  text-align: center;
  height: 8rem;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  width: 8rem;
  border-radius: 50%;
  margin-top: 30px;
  margin-left: 0px;
  cursor: pointer
}
.transfer-button:hover {
  background-color: #a40a7d
}
.chain-logo {
  cursor: pointer;
}
.chain-logo > svg {
  height: 8rem;
  width: 8rem;
}
.chain-logo-inactive > svg {
  height: 2.5rem;
  width: 2.5rem;
}
.chain-logo-inactive > svg > g > path {
  fill: black;
}
.chain-logo-active > svg > g > path {
  fill: none;
}
.chain-logo-active {
  cursor: help;
}
.nextToEachOther2 {
  display: inline-block;
}

